import Table from 'react-bootstrap/Table';
import { useDispatch, useSelector } from 'react-redux';
import { changeName, increase, addCount } from './store';

function Cart() {

    let state = useSelector((state)=>{return state})
    //state store   state , ((state)=>{return state.user})  
    //js((state)=>{return state}) ((state)=> state)  
    // console.log(state.cart[0].name)
    // // store   state  hook

    let dispatch = useDispatch()

    return (
        <div>
          <h6>
            {state.user.name} {state.user.age} 
            </h6>
            <button onClick={() =>{
              dispatch(increase(2))
            }}></button>
          

            <Table striped bordered hover>
      <thead>
        <tr>
          <th>#</th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {
          state.cart.map((a, i) => //{return } , js
        <tr key={i}>
          <td>{state.cart[i].id}</td>
          <td>{state.cart[i].name}</td>
          <td>{state.cart[i].count}</td>
          <td>
            <button onClick={()=>{
              dispatch(addCount(state.cart[i].id)) //id      
              //chageName()  store.js 
            }}>+</button>
          </td>
        </tr>
          )
        }
        
        {/* <tr>
          <td>2</td>
          <td>{state.cart[1].id}</td>
          <td>{state.cart[1].name}</td>
          <td>{state.cart[1].count}</td>
        </tr>
        <tr>
          <td>3</td>
          <td colSpan={2}>Larry the Bird</td>
          <td>@twitter</td>
        </tr> */}
      </tbody>
    </Table>
        </div>
    )
}

export default Cart